<template>
    <div>

<nav-bar :name="title"></nav-bar>

            <v-card class="ma-1" cols="12">
            <v-container>
                    <v-col cols="12">
                        <v-select
                        class="mb-n5"
                        outlined
                        dense
                        :items="invoices"
                        v-model="items_payment.invoice"
                        label="*Invoice"
                        ></v-select>
                    </v-col>
                    <v-col cols="12">
                        <v-select
                        class="mb-n5"
                        outlined
                        dense
                        :items="items"
                        v-model="items_payment.method"
                        label="*Método de pago"
                        ></v-select>
                    </v-col>
                    <v-col cols="12">
                        <v-text-field
                        class="mb-n5"
                        outlined
                        dense
                        v-model="items_payment.amount"
                        label="*Amount"
                        prepend-inner-icon="mdi-currency-usd"
                        ></v-text-field>
                    </v-col>
                    <v-col
                    cols="12"
                    >
                    <v-menu
                        ref="menu"
                        v-model="menu"
                        :close-on-content-click="false"
                        :return-value.sync="items_payment.date"
                        transition="scale-transition"
                        offset-y
                        min-width="auto"
                    >
                        <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                            class="mb-n5"
                            v-model="items_payment.date"
                            prepend-inner-icon="mdi-calendar"
                            outlined
                            dense
                            v-bind="attrs"
                            v-on="on"
                        ></v-text-field>
                        </template>
                        <v-date-picker
                        v-model="items_payment.date"
                        no-title
                        scrollable
                        >
                        <v-spacer></v-spacer>
                        <v-btn
                            text
                            color="primary"
                            @click="menu = false"
                        >
                            Cancel
                        </v-btn>
                        <v-btn
                            text
                            color="primary"
                            @click="$refs.menu.save(items_payment.date)"
                        >
                            OK
                        </v-btn>
                        </v-date-picker>
                    </v-menu>
                    </v-col>
                    <v-col cols="12">
                        <v-text-field
                        class="mb-n5"
                        outlined
                        dense
                        v-model="items_payment.reference"
                        label="*Reference"
                        ></v-text-field>
                    </v-col>
                    <v-col>
                    <v-btn
                    class="mx-4 my-2"
                    color="#989898"
                    dark
            @click="$router.go(-1)"
                    >
                    GO BACK
                    </v-btn>
                    <v-btn
                    class="mx-4 my-2"
                    color="#E47911"
                    dark
                    :loading="loading"
                    @click="send_report_payment"
                    >
                    SEND REPORT
                    </v-btn>
                    </v-col>
            </v-container>
            </v-card>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import InvoiceService from '@/services/InvoiceService';
import PaymentService from '@/services/PaymentService';
import Swal from 'sweetalert2'

export default {
    components: {
        NavBar
    },

    data: () => ({
        title: 'REPORT PAYMENT',
        items: [
            { text: 'Transfer', value: "T" },
            { text: 'Cash', value: "C" },
            { text: 'Deposit', value: "D" },
            { text: 'Other', value: "O" },
            ],
        payment: '',
        items_payment: {
            amount: 0,
            date: new Date().toISOString().substr(0, 10),
            reference: '',
            invoice: '',
            method: 'T',
            status: 'Pd',
        },
        menu: false,
        invoices: [ ],
        loading: false,
    }),

    computed:{
        idStore(){
            return this.$store.state.auth.store_user.unique_id;
        }
    },

    mounted(){
        InvoiceService
        .list_pending(this.idStore)
        .then(res => {
            res.forEach(item => {
                this.invoices.push({ text: 'Invoice: ' + item.unique_id , value: item.unique_id})
            });
        })
        .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
    },


    methods: {
        send_report_payment(){
            this.loading = true;
            PaymentService
            .send_report_payment(this.items_payment)
            .then(() => {
                this.loading = false;
                Swal.fire({
                    icon: 'success',
                    title: 'Success',
                    text: 'The report payment has created.'
                }).then(() => {
                    return this.$router.push('/payments')
                })
            })
            .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
        }
    }
    
}
</script>